import Criteria from "./Criteria";

function Criterias(props) {


  return (
    <div className={"criterias " + props.tab}>
      <Criteria
        id="0"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[0]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[0]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Hat der Kunde die Unterstützung der Geschäftsführung für ein as-a-Service Modell?"
      />
      <Criteria
        id="1"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[1]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[1]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Sieht der Kunde die Mehrwerte des as-a-Service Konzeptes?"
      />
      <Criteria
        id="2"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[2]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[2]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Hat der Kunde die Herausforderung der Überprovisionierung?"
      />
      <Criteria
        id="3"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[3]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[3]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Ist die Auslastung der IT-Mannschaft hoch?"
      />
      <Criteria
        id="4"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[4]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[4]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Hat der Kunde Geschäftsanforderungen, die eine höhere Flexibilität und Agilität der IT erfordern?"
      />
      <Criteria
        id="5"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[5]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[5]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Hat der Kunde eine Diskrepanz zwischen Erwartung und Erbringung von internen IT Diensleistungen (SLA's)?"
      />
      <Criteria
        id="6"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[6]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[6]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Existieren Budgetbeschränkungen?"
      />
      <Criteria
        id="7"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[7]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[7]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Benötigt der Kunde eine Bessere Interne Verrechnung der IT-Kosten (Transparenz)?"
      />
      <Criteria
        id="8"
        callbackCriterias={props.callbackCriterias}
        criteriaValue={props.criteriaValues[8]}
        callbackWeightings={props.callbackWeightings}
        weighting={props.weightings[8]}
        tab={props.tab}
        editableWeightings={props.editableWeightings}
        text="Existieren zu lange Beschaffungsprozesse/ Sollen Beschaffungsprozesse verkürzt werden?"
      />
    </div>
  );
}

export default Criterias;