import Criterias from "./Criterias";
import { useState } from 'react';

function Settings(props) {

  const [editableWeightings, seteditableWeightings] = useState(false);

  const [weightingsCopy, setweightingsCopy] = useState(props.weightings)

  const checkWeightings = () => {
    let sum = weightingsCopy.reduce((pv, cv) => parseFloat(pv) + parseFloat(cv), 0);
    if (sum === 1) {
      props.callbackWeightings(weightingsCopy);
      return true;
    }
    alert("Die Summe der Gewichtungen ist ungleich 1!");
    return false;
  };

  const updateWeightingsCopy = (value, index) => {
    let weightings = [].concat(weightingsCopy);
    weightings[index] = value;
    setweightingsCopy(weightings);
  };

  const updateEditable = (e) => {
    if (editableWeightings) {
      if (checkWeightings() === true) {      
        e.target.innerHTML = "Bearbeiten";     
        seteditableWeightings(!editableWeightings);
      }
    } else {
      e.target.innerHTML = "Speichern";
      seteditableWeightings(!editableWeightings);
    }
  };

  return (
    <div className="settings">
      <div className="settingsCriteria">
        <p>Gewichtungen der Kriterien anpassen:</p>
        <Criterias
          callbackCriterias={props.callbackCriterias}
          criteriaValues={props.criteriaValues}
          tab={props.tab}
          weightings={weightingsCopy}
          callbackWeightings={updateWeightingsCopy}
          editableWeightings={editableWeightings}
        />
        <button className="editWeightings" onClick={(e) => updateEditable(e)}>
          Bearbeiten
        </button>
        <p className="sumWeightings">
          Summe: {props.weightings.reduce((pv, cv) => pv + cv, 0)}
        </p>
      </div>
      <div className="settingsCosts">
        <p>Kosten für Infrastruktur anpassen: </p>
        <table className="settingCostTable">
          <thead>
            <tr>
              <td></td>
              <th scope="col">Arbeitsspeicher (pro GB)</th>
              <th scope="col">vCPU (pro Stück)</th>
              <th scope="col">Speicher (pro GB)</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th scope="row">Kauf HPE Infrastruktur</th>
              <td>
                <input
                  value={props.costHardware[0][0]}
                  onChange={(e) =>
                    props.callbackCostHardware(parseFloat(e.target.value), 0, 0)
                  }
                />
              </td>
              <td>
                <input
                  value={props.costHardware[0][1]}
                  onChange={(e) =>
                    props.callbackCostHardware(parseFloat(e.target.value), 0, 1)
                  }
                />
              </td>
              <td>
                <input
                  value={props.costHardware[0][2]}
                  onChange={(e) =>
                    props.callbackCostHardware(parseFloat(e.target.value), 0, 2)
                  }
                />
              </td>
            </tr>
            <tr>
              <th scope="row">HPE GreenLake</th>
              <td>
                <input
                  value={props.costHardware[1][0]}
                  onChange={(e) =>
                    props.callbackCostHardware(parseFloat(e.target.value), 1, 0)
                  }
                />
              </td>
              <td>
                <input
                  value={props.costHardware[1][1]}
                  onChange={(e) =>
                    props.callbackCostHardware(parseFloat(e.target.value), 1, 1)
                  }
                />
              </td>
              <td>
                <input
                  value={props.costHardware[1][2]}
                  onChange={(e) =>
                    props.callbackCostHardware(parseFloat(e.target.value), 1, 2)
                  }
                />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Settings;