import Criterias from "./Criterias";
import QualificationCriteria from "./QualificationCriteria";
import { useState } from "react";

function InputCriteria(props) {

  const [showNotice, setshowNotice] = useState(false)

  const submitqualificationCriterias = () => {
    props.callbackExpandForm(true);
    setshowNotice(true);
  }

  const notice = () => {
    let text = "";
    if(props.qualificationValues[0] < 150) {
      text += "Projektvolumen von mindestens 150 Tausend Euro empfohlen!";
    }
    if (props.qualificationValues[1] < 15) {
      text += "\nWachstumsquote von mindestens 15% empfohlen!";
    }
    if (text === "") {
      return <div className="qualificationNotice green"><p>Empfohlene Mindestanforderungen für Projektvolumen und Wachstumsquote erfüllt!</p></div>;
    }
    else {
      return <div className="qualificationNotice red"><p>{text}</p></div>;
    }
  }
  

  return (
    <div>
      <div className="qualificationSection">
        <QualificationCriteria
          id="0"
          callbackQualification={props.callbackQualification}
          qualificationValues={props.qualificationValues[0]}
          text="Projektvolumen:"
        />
        <QualificationCriteria
          id="1"
          callbackQualification={props.callbackQualification}
          qualificationValues={props.qualificationValues[1]}
          text="Wachstumsquote:"
        />
        <button
          className="continue"
          id="btnToExpandInput"
          disabled={props.qualificationValues[0] === "" || props.qualificationValues[1] === "" ? true : false}
          onClick={() => submitqualificationCriterias()}
        >
          Prüfen
        </button>
        {showNotice && (
          notice()
        )}
      </div>
      {props.expandForm && (
        <div className="expandedForm">
          <Criterias
            callbackCriterias={props.callbackCriterias}
            criteriaValues={props.criteriaValues}
            tab={props.tab}
            weightings={props.weightings}
          />
          <button
            className="continue"
            id="btnToCapacity"
            onClick={() => props.callbackInputTab("Capacity")}
          >
            Fortsetzen
          </button>
        </div>
      )}
    </div>
  );
}

export default InputCriteria;