import { useState } from "react";

function ScoreBar(props) {

  const [style, setstyle] = useState({})

  let sum = 0;
  let values = props.criteriaValues;
  let weightings = props.weightings;
  let classname = "scoreValue ";

  for (let i = 0; i < values.length; i++) {
    if(values[i] >= 0) {
      sum += values[i] * weightings[i];
    }
  }

  if (sum >= 0.5) {
    classname += "green";
  } else if (sum >= 0.01) {
    classname += "red";
  }

  setTimeout(() => {
    const newStyle = {
      width: sum * 100 + "%"
    }
    setstyle(newStyle);
  });

  return (
    <div className="scoreBar">
      <div className={classname} style={style}>{(sum * 100).toFixed(2)}%</div>
    </div>
  );
}

export default ScoreBar;