import './App.css';
import './Sidebar.css';
import './Criterias.css';
import './QualificationCriteria.css';
import './ScoreBar.css';
import './Capacities.css';
import './Settings.css';
import Form from './components/Form';
import Sidebar from './components/Sidebar';
import { useState } from "react";

function App() {
  
  const [sidebarTab, setsidebarTab] = useState(1)

  const updateTab = (tabParameter) => {
    setsidebarTab(tabParameter);
  }

  
  
  return (
    <div className="App">
      <Sidebar callbackTab={updateTab} />
      <Form tab={sidebarTab} callbackTab={updateTab} />
    </div>
  );
}

export default App;
