function QualificationCriteria(props) {

  const updateQualification = (value) => {
    props.callbackQualification(value, props.id);
  }

  return (
    <div className="qualificationCriteria">
      <input
        className="qualificationInput"
        type="number"
        id={props.id}
        value={props.qualificationValues}
        placeholder="  "
        onChange={(e) => updateQualification(e.target.value)}
      />
      <label className="qualificationLabel" htmlFor={props.id}>{props.text}</label>
    </div>
  );
}

export default QualificationCriteria;