import logo from "../SanDataLogo.png";

function Home(props) {


  return (
    <div className="home">
      <img src={logo}></img>
      <p>
        Dieses Tool dient als Hilfe bei der Entscheidung über die Bezugsart von
        IT-Infrastruktur. Dabei werden in zwei Arten Ergebnisse generiert. Im
        ersten Abschnitt müssen Fragen beantwortet werden, die den technischen
        Nutzen von verbrauchsbasierten IT-Services in Form eines Scores
        bewerten. Im zweiten Abschnitt werden die Kosten der beiden Bezugsarten
        ermittelt. Nach Eingabe der benötigten Kapazitäten und Rahmendaten für
        die neue IT-Infrastruktur, werden jeweils Gesamtergebnisse über die
        ungefähren zu erwartenden Kosten berechnet.
      </p>
      <p>
        Zum Navigieren können die auf den Seiten jeweiligen Knöpfe verwendet
        werden, die Schritt für Schritt durch das Tool leiten. Nach den
        erwähnten Eingaben wird eine Ergebnisübersicht angezeigt. Um Eingaben zu
        verändern kann das Navigationsmenü auf der linken Seite verwendet 
        werden. Hierüber lässt sich jede Ansicht der Anwendung erreichen.
      </p>
      <button className="continue" onClick={() => props.callbackStart()}>Neue Eingabe starten</button>
    </div>
  );
}

export default Home;
