

function Criteria(props) {

  const updateCriteria = (value) => {
    props.callbackCriterias(value, props.id);
  }

  const updateWeighting = (value) => {
    props.callbackWeightings(value, props.id);
  };

  const output = () => {
    if (props.tab === 2) {
      return (
        <div className={"criteria " + props.tab}>
          <p>{props.text}</p>
          <span className="radioCriteriaContainer">            
            <input
              className={"radioCriteria " + props.id}
              type="radio"
              name={props.id}
              id={"Ja " + props.id}
              checked={props.criteriaValue === 1 ? true : false}
              onChange={() => updateCriteria(1)}
            />
            <label htmlFor={"Ja " + props.id} className="labelJa">
              <span className="radioText Ja">Ja</span>
            </label>
            <input
              className="radioCriteria"
              type="radio"
              name={props.id}
              id={"Nein " + props.id}
              checked={props.criteriaValue === 0 ? true : false}
              onChange={() => updateCriteria(0)}
            />
            <label htmlFor={"Nein " + props.id} className="labelNein">
              <span className="radioText Nein">Nein</span>
            </label>
          </span>
        </div>
      );
    } else if(props.tab === 3){
       
      if (props.criteriaValue === 1) {
        return (
          <div className={"criteria " + props.tab}>
            <p>{props.text}</p>
            <p>{props.weighting}</p>
            <p className="criteriaJaP">Ja</p>
          </div>
        );
      }
      else if (props.criteriaValue === -1) {
        return (
          <div className={"criteria " + props.tab}>
            <p>{props.text}</p>
            <p>{props.weighting}</p>
            <p>-</p>
          </div>
        );
      }
      else {
        return (
          <div className={"criteria " + props.tab}>
            <p>{props.text}</p>
            <p>{props.weighting}</p>
            <p className="criteriaNeinP">Nein</p>
          </div>
        );
      }
    } else {
      return (
        <div className={"criteria " + props.tab}>
          <p>{props.text}</p>
          <p>Gewichtung: </p>
          <input
            value={props.weighting}
            type="number"
            onChange={(e) => updateWeighting(parseFloat(e.target.value))}
            disabled={props.editableWeightings ? false : true}
          />
        </div>
      );
    }
  } 

  return (
    <div className={"childCriteria " + props.tab}>
      {output()}
    </div>
  );
}

export default Criteria;