import InputCriteria from "./InputCriteria";
import InputCapacity from "./InputCapacity";

function Input(props) {
  

  return (
    <div className="input">
      <ul className="inputNavbar">
        <li
          id={
            props.inputTab === "Criteria"
              ? "inputTabActive"
              : "inputTabNotActive"
          }
          onClick={() => props.callbackInputTab("Criteria")}
        >
          Kriterien
        </li>
        <li
          id={
            props.inputTab === "Capacity"
              ? "inputTabActive"
              : "inputTabNotActive"
          }
          onClick={() => props.callbackInputTab("Capacity")}
        >
          Kapazitäten
        </li>
      </ul>
      {props.inputTab === "Criteria" && (
        <InputCriteria
          callbackCriterias={props.callbackCriterias}
          criteriaValues={props.criteriaValues}
          callbackQualification={props.callbackQualification}
          qualificationValues={props.qualificationValues}
          callbackExpandForm={props.callbackExpandForm}
          expandForm={props.expandForm}
          tab={props.tab}
          callbackInputTab={props.callbackInputTab}
          weightings={props.weightings}
        />
      )}
      {props.inputTab === "Capacity" && (
        <InputCapacity
          callbackTab={props.callbackTab}
          capacityVMs={props.capacityVMs}
          callbackVMs={props.callbackVMs}
          capacityDetail={props.capacityDetail}
          callbackCapacityDetail={props.callbackCapacityDetail}
          capacities={props.capacities}
          callbackCapacities={props.callbackCapacities}
          growthRate={props.qualificationValues[1]}
        />
      )}
    </div>
  );
}

export default Input;