import logo from "../SanDataLogo.png";
import { useState } from "react";

function Sidebar(props) {

  const [sidebarActive, setsidebarActive] = useState(false)

  const toggleSidebar = () => {
    setsidebarActive(!sidebarActive);
  }

  return (
    <div className={sidebarActive ? "sidebar active" : "sidebar"}>
      <div className="logo_content">
        <div className="logo">
          <img src={logo}></img>
          <div className="logo_name">SanData</div>
        </div>
        <i className="bx bx-menu" id="menu-btn" onClick={() => toggleSidebar()}></i>
      </div>
      <ul>
        <li onClick={() => props.callbackTab(1)}>
          <a href="#">
            <i className="bx bxs-home"></i>
            <span className="links_name">Startseite</span>
          </a>
          <span className="tooltip">Startseite</span>
        </li>
        <li onClick={() => props.callbackTab(2)}>
          <a href="#">
            <i className="bx bx-task"></i>
            <span className="links_name">Eingabe</span>
          </a>
          <span className="tooltip">Eingabe</span>
        </li>
        <li onClick={() => props.callbackTab(3)}>
          <a href="#">
            <i className="bx bx-bar-chart-alt-2"></i>
            <span className="links_name">Ergebnis</span>
          </a>
          <span className="tooltip">Ergebnis</span>
        </li>
        <li onClick={() => props.callbackTab(4)}>
          <a href="#">
            <i className="bx bx-cog"></i>
            <span className="links_name">Einstellungen</span>
          </a>
          <span className="tooltip">Einstellungen</span>
        </li>
      </ul>
    </div>
  );
}

export default Sidebar;