import Input from "./Input";
import Home from "./Home";
import Result from "./Result";
import Settings from "./Settings";
import { data } from "../data";
import { useState, useEffect } from "react";

function Form (props) {

  const [criteriaValues, setcriteriaValues] = useState(() => {return JSON.parse(localStorage.getItem("criterias")) || [-1,-1,-1,-1,-1,-1,-1,-1,-1]});

  const [qualificationValues, setqualificationValues] = useState(() => {return JSON.parse(localStorage.getItem("qualification")) || ["",""]});

  const [expandForm, setexpandForm] = useState(() => {return JSON.parse(localStorage.getItem("expandForm")) || false});

  const [capacityDetail, setcapacityDetail] = useState(() => {return JSON.parse(localStorage.getItem("capacityDetail")) || false});

  const [inputTab, setinputTab] = useState("Criteria");

  const [weightings, setweightings] = useState(data["weightings"]);

  const [capacities, setcapacities] = useState(() => {return JSON.parse(localStorage.getItem("capacities")) || [0,0,0,0,3]});

  const [capacityVMs, setcapacityVMs] = useState(() => {return JSON.parse(localStorage.getItem("capacityVMs")) || [
    { memory: 200, cpu: 3, storage: 300, amount: 5 },
    { memory: 254, cpu: 1, storage: 340, amount: 2 },
    { memory: 107, cpu: 2, storage: 212, amount: 1 },
    { memory: 600, cpu: 5, storage: 270, amount: 3 },
    { memory: 176, cpu: 1, storage: 100, amount: 4 },
  ]});

  const [costHardware, setcostHardware] = useState(data["costs"]);

  useEffect(() => {localStorage.setItem("criterias", JSON.stringify(criteriaValues));}, [criteriaValues]);
  useEffect(() => {localStorage.setItem("qualification", JSON.stringify(qualificationValues));}, [qualificationValues]);
  useEffect(() => {localStorage.setItem("expandForm", JSON.stringify(expandForm));}, [expandForm]);
  useEffect(() => {localStorage.setItem("capacities", JSON.stringify(capacities));}, [capacities]);
  useEffect(() => {localStorage.setItem("capacityDetail", JSON.stringify(capacityDetail));}, [capacityDetail]);
  useEffect(() => {localStorage.setItem("capacityVMs", JSON.stringify(capacityVMs));}, [capacityVMs]);

  const updateCriterias = (value, id) => {
    let values = [].concat(criteriaValues);
    values[id] = value;
    setcriteriaValues(values);
  };

  const updateQualification = (value, id) => {
    let values = [].concat(qualificationValues);
    values[id] = value;
    setqualificationValues(values);
  };

  const updateExpandForm = (value) => {
    setexpandForm(value)
  };

  const updateCapacityDetail = () => {
    setcapacityDetail(!capacityDetail);
  };

  const updateInputTab = (value) => {
    setinputTab(value);
  };

  const updateWeightings = (weightings) => {
    setweightings(weightings);
  };

  const updateCapacityVMs = (vm) => {
    let vms = [].concat(capacityVMs);
    if (vm === "delete") {
      vms.pop();
    } else {
      vms = vms.concat(vm);
    }
    setcapacityVMs(vms);
  };

  const updateCapacities = (capacity, index = -1) => {
    if (index === -1) {
      setcapacities(capacity);
    } else {
      let capacitiesCopy = [].concat(capacities);
      capacitiesCopy[index] = capacity;
      setcapacities(capacitiesCopy);
    }
  };

  const updateCostHardware = (value, array, index) => {
    let costs = [].concat(costHardware);
    costs[array][index] = value;
    setcostHardware(costs);
    console.log(costHardware);
  };

  const startTool = () => {
    props.callbackTab(2);
    setinputTab("Criteria");
    setcriteriaValues([-1, -1, -1, -1, -1, -1, -1, -1, -1]);
    setqualificationValues(["", ""]);
    setexpandForm(false);
    setcapacityDetail(false);
    setcapacities([0, 0, 0, 0, 3]);
    setcapacityDetail(false);
    setcapacityVMs([
      { memory: 200, cpu: 3, storage: 300, amount: 5 },
      { memory: 254, cpu: 1, storage: 340, amount: 2 },
      { memory: 107, cpu: 2, storage: 212, amount: 1 },
      { memory: 600, cpu: 5, storage: 270, amount: 3 },
      { memory: 176, cpu: 1, storage: 100, amount: 4 },
    ]);
  };
  
  const output = () => {
    switch(props.tab) {
        case 1:
          return <Home callbackStart={startTool} />;
           
        case 2:
          return (
            <Input
              callbackCriterias={updateCriterias}
              criteriaValues={criteriaValues}
              callbackQualification={updateQualification}
              qualificationValues={qualificationValues}
              callbackExpandForm={updateExpandForm}
              expandForm={expandForm}
              tab={props.tab}
              weightings={weightings}
              callbackTab={props.callbackTab}
              capacityVMs={capacityVMs}
              callbackVMs={updateCapacityVMs}
              inputTab={inputTab}
              callbackInputTab={updateInputTab}
              capacityDetail={capacityDetail}
              callbackCapacityDetail={updateCapacityDetail}
              capacities={capacities}
              callbackCapacities={updateCapacities}
            />
          );
        
        case 3:
          return (
            <Result
              criteriaValues={criteriaValues}
              tab={props.tab}
              weightings={weightings}
              costHardware={costHardware}
              capacities={capacities}
              growthRate={qualificationValues[1]}
            />
          );
        
        case 4:
          return (
            <Settings
              criteriaValues={criteriaValues}
              tab={props.tab}
              callbackWeightings={updateWeightings}
              weightings={weightings}
              callbackCostHardware={updateCostHardware}
              costHardware={costHardware}
            />
          );
        
        default:
          break;
      }
  }
  
  return (
    <div className="form">
      {output()}
    </div>
  )
}

export default Form;